    .footer-container{
        display: flex;
        flex: 1 1 100%;
        flex-direction: row;
        position: relative;
        bottom: -75%;
        justify-content: space-between;
        color: #232227;
        align-items: center;
        background: rgba(196, 196, 196, 0.4);
        padding: 0px;
        &__social-media{
            display: flex;
            flex-direction: row;
            padding-top: 5px;
            padding-bottom: 5px;
            padding-left: 30px;
            img{
                border-radius: 50%;
                padding-bottom: 0px;
                width: 26px;
            }
        }
        &__established{
            align-items: center;
            display: flex;
            flex-direction: row;
            font-size: 20px;
            text-transform: lowercase;
            padding-top: 5px;
            padding-bottom: 5px;
            padding-right: 30px;
            .policy{
                display: flex;
                padding-right: 50px;
                text-transform: none;
                p{
                    padding-right: 10px;
                    font-weight: 500;
                }
            }
            .copyright{
                font-size: 12px;
            }
            
        }
        a{
            padding: 2px;
        }
        .triangle{
            padding: 0px;
            width: 0;
            height: 0;
            border-bottom: 478px solid #F86C59;
            border-left: 867px solid transparent;
            position: absolute;
            bottom: 0;
            right: 1px;
            z-index: -1;
      
          }
    }
